#Contact {
    width: 100%;
    height: 100vh;
    position: relative; 
    background: rgb(8,0,0);
    background: linear-gradient(90deg, rgba(8,0,0,0.981127485173757) 20%, rgba(16,17,16,1) 52%, rgba(16,17,16,1) 54%, rgba(4,5,4,1) 73%, rgba(4,8,4,1) 81%, rgba(14,14,14,1) 88%);
    font-family:'Sacramento', cursive;
}
#Contact .heading {
    position: absolute;
    top: 50vh;
    left: 0;
    transform: translateY(-50%);
}

#Contact h1 {
    color: rgba(156, 149, 149, 0.192);
    writing-mode: vertical-lr;
    text-orientation: upright;
    letter-spacing: -10px;
}
#Contact .social-container {
    width: 80%;
    position: absolute;
    top: 25vh;
    left: 10%;
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
}

#Contact a {
    width: 50%;
    text-decoration: none;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    padding: 1.5rem 0;
    color: rgb(229,229,229);
    font-family:'Sacramento', cursive;
    transition: color .3s;
}

#Contact span {
    margin-left: 1rem;
}
#Contact a:hover {
    color: rgb(190, 192, 192);
}


   /* Responsive design */

@media screen and (width:600px) {
    #Contact .heading h1 {
        letter-spacing: -6px;
        font-size:large;
    }
     #Contact .social-container {
        position: absolute;
        top: 20vh;
        left: 13%;
    }
    #Contact a {
        font-size: 1rem;
        padding: .8rem 0;
        width: 80%;
    }
    #Contact span {
        margin-left: .5rem;
    }
    .icons {
        height: 25px;
    }

}


