#Navbar {
  position: fixed;
  left: 0px;
  top: 0;
  z-index: 9999;
  width: 100%;
  background: linear-gradient(to bottom right, #000000, #070707);
  box-shadow: 10px 0 20px 10px rgb(10, 10, 10);

}
#Navbar .nav-content {
  display: none;
} 
#Navbar .nav-list {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    width: 50%; 
    margin: 1.2rem 0 .9rem 5rem; 
  }
#Navbar .nav-list li {
    padding: .2rem 1rem;
    list-style-type: none;
    border-radius: 50px;
    font-size: 1rem;
    letter-spacing: 2px; 
    
    display:inline-block; 
    margin:0; 
  }

  #Navbar .nav-list li:after {
    content: '';
    display: block;
    width: 0;
    height: 2px;
    margin: auto;                     /*   try without this, starts from left to right */
    background: rgb(209, 137, 3);
    transition: width .3s;
  }

  #Navbar li:hover::after {  
    width: 100%;
  }
  #Navbar li a {
    color: rgb(229,229,229);
    font-family:'Sacramento', cursive;
  }


  #Navbar .brand-name {
    width: 30px;
    position: absolute;
    right: 0;
    margin: 1.5rem 4rem .5rem 0; 
    font-family: 'Cedarville Cursive', cursive;
    color: rgb(229,229,229);
    color: orange;
    font-size: 1.1rem;
    cursor: pointer;
    -webkit-animation: bounce 2s  infinite linear;
            animation: bounce 2s  infinite linear;
  }
  @-webkit-keyframes bounce {
      0%   { -webkit-transform: translateY(0); transform: translateY(0); }
      50%  { -webkit-transform: translateY(-20px); transform: translateY(-20px); }
      100% { -webkit-transform: translateY(0); transform: translateY(0); }
  }
  @keyframes bounce {
      0%   { -webkit-transform: translateY(0); transform: translateY(0); }
      50%  { -webkit-transform: translateY(-20px); transform: translateY(-20px); }
      100% { -webkit-transform: translateY(0); transform: translateY(0); }
  }
  
  /* #Navbar ul li.active {
    background-color: crimson;
    transition: 0.3s ease background-color;
  } */

  @media screen and (max-width:600px) {
     #Navbar ul.nav-list, .brand-name {
